<template>
  <div class="wrap_button">
    <div class="buttons_container">
      <div
        class="button"
        v-for="(button, index) in buttons"
        :key="index"
        @click="setItemActive(index)"
        :class="{active: button.active, no_active: noActivityButtons}"
        ref="button"
      >
        <div class="icon" :class="button.icon"></div>
        <span class="text_button">{{button.text}}</span>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  props: ["password", "buttons", "noActivityButtons"],
  data() {
    return {};
  },
  methods: {
    setItemActive(index) {
      this.buttons[index].active = !this.buttons[index].active;
      // this.$refs.button.classList.remove('no_active')
      this.password.push(index);
      var temp = this.password.join("");
      if (temp === "501444553") {
        this.$emit("toggleView");
      }
      this.$emit("set-no-active");
    },
    pickUpSelectedItems() {
      var countActiveBtn = 0;
      this.buttons.forEach(element => {
        if (element.active) {
          if (!localStorage.getItem(element.text)) {
            localStorage.setItem(element.text, 1);
            element.count = 1;
          } else {
            var count = parseInt(localStorage.getItem(element.text));
            localStorage.setItem(element.text, ++count);
            element.count = count;
          }
          countActiveBtn++;
        }

        element.active = false;
      });

      if (countActiveBtn === 0) {
      } else {
        var count = parseInt(localStorage.getItem("people"));

        if (!count) {
          localStorage.setItem("people", 1);
        } else {
          localStorage.setItem("people", ++count);
        }
      }
    }
  },

  mounted() {
    this.buttons.forEach(element => {
      var button = element.text;
      if (localStorage.getItem(button)) {
        var count = localStorage.getItem(button);
        count = parseInt(count);
        element.count = count;
      }
    });
  }
};
</script>

<style lang="less">
.wrap_button {
  .buttons_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;

    padding: 15px;

    .button {
      flex-grow: 1;
      flex-shrink: 0;
      height: 150px;
      border-radius: 8px;
      width: 200px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      background: #47631d;
      border: 1px solidrgba(174, 201, 55, 0.75);
      padding: 12px 14px;
      margin-left: 4px;
      margin-top: 4px;
      &.no_active {
        transition: all 0.5s;
        animation: tada 10s infinite;
      }

      .text_button {
        color: #ffffff;
        text-transform: uppercase;
        font-weight: bold;
        letter-spacing: 0.06em;
        font-size: 33px;
        text-align: center;
        padding: 5px 0;
      }
      .icon {
        flex-shrink: 0;
        width: 70px;
        height: 70px;
      }

      .journals {
        background: url("../../img/mag_.svg") no-repeat;
      }
      .reclaim {
        background: url("../../img/campaign_.svg") no-repeat;
      }
      .vk {
        background: url("../../img/smartphone_.svg") no-repeat;
      }
      .radio {
        background: url("../../img/radio_.svg") no-repeat;
      }
      .tv {
        background: url("../../img/television_.svg") no-repeat;
      }
      .newspaper {
        background: url("../../img/newspaper_.svg") no-repeat;
      }
    }
    .active {
      transition: all 0.5s;
      background: rgba(174, 201, 55, 0.75);
      position: relative;
      overflow: hidden;
    }
  }
}
@keyframes tada {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  1%,
  2% {
    -webkit-transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
  }

  3%,
  5%,
  7%,
  9% {
    -webkit-transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, 3deg);
    transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, 3deg);
  }

  4%,
  6%,
  8% {
    -webkit-transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(1, 1, 1) rotate3d(0, 0, 1, -3deg);
  }
  10% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

@media screen and (max-width: 568px) {
  .wrap_button {
    .buttons_container {
      .button {
        height: 63px;
        width: 100px;
        .text_button {
          letter-spacing: 0.02em;
          font-size: 16px;
        }
        .icon {
          width: 36px;
          height: 36px;
        }
      }
    }
  }
}
</style>
