
<template>
  <div class="wrap_chart">
    <div class="cross" @click="$emit('closeGraph')">
      <svg
        width="26"
        height="26"
        viewBox="0 0 26 26"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          fill-rule="evenodd"
          clip-rule="evenodd"
          d="M13.2499 16.7856L22.2655 25.8011L25.801 22.2656L16.7855 13.2501L25.801 4.23449L22.2655 0.698959L13.2499 9.71452L4.23426 0.698853L0.69873 4.23439L9.7144 13.2501L0.698731 22.2657L4.23427 25.8013L13.2499 16.7856Z"
          fill="white"
        ></path>
      </svg>
    </div>
    <div id="chartdiv"></div>
    <div class="people">Количество проголосовавших людей - {{people}}</div>

    <div class="btn_clear" @click="clearStatistic">Сбросить статистику</div>

    <div class="wrap_confirm" v-if="showConfirm">
      <div class="popup_confirm">
        <div class="btn yes" @click="clean">Очистить!</div>
        <div class="btn no" @click="cancel">Отмена</div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import am4themes_material from "@amcharts/amcharts4/themes/material";
am4core.useTheme(am4themes_material);
am4core.useTheme(am4themes_animated);
export default {
  props: [""],
  data() {
    return {
      people: localStorage.getItem("people") || 0,
      showConfirm: false,
      chart: ""
    };
  },
  methods: {
    clearStatistic() {
      this.showConfirm = !this.showConfirm;
    },
    cancel() {
      this.showConfirm = !this.showConfirm;
    },
    clean() {
      this.showConfirm = !this.showConfirm;
      localStorage.clear();
      this.updateGraph;
    }
  },
  computed: {
    setDataProvider() {
      return [
        {
          item: "Журналы",
          value: localStorage.getItem("Журналы") || 0
        },
        {
          item: "Наружная реклама",
          value: localStorage.getItem("Наружная реклама") || 0
        },
        {
          item: "Соц.сети",
          value: localStorage.getItem("Соц.сети") || 0
        },
        {
          item: "Радио",
          value: localStorage.getItem("Радио") || 0
        },
        {
          item: "Телевидение",
          value: localStorage.getItem("Телевидение") || 0
        },
        {
          item: "Газеты",
          value: localStorage.getItem("Газеты") || 0
        }
      ];
    },
    updateGraph() {
      window.chart.data = [];
      window.chart.validateData();
    }
  },
  mounted() {
    window.chart = am4core.create("chartdiv", am4charts.PieChart);
    chart.legend = new am4charts.Legend();
    chart.legend.labels.template.text = "{item} - {value}";

    chart.data = this.setDataProvider;
    // Set inner radius
    chart.innerRadius = am4core.percent(40);

    // Add and configure Series
    window.pieSeries = chart.series.push(new am4charts.PieSeries());
    //pieSeries.slices.template.stroke = am4core.color("#fff");
    pieSeries.alignLabels = false;

    pieSeries.slices.template.strokeWidth = 2;
    pieSeries.slices.template.strokeOpacity = 1;
    pieSeries.color = am4core.color("#ffffff");

    // This creates initial animation
    pieSeries.hiddenState.properties.opacity = 1;
    pieSeries.hiddenState.properties.endAngle = -90;
    pieSeries.hiddenState.properties.startAngle = -90;

    pieSeries.dataFields.value = "value";
    pieSeries.dataFields.category = "item";

    this.chart = chart;
  }
};
</script>

<style lang="less">
.wrap_chart {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  .cross {
    position: absolute;
    top: 0px;
    right: 0px;
    padding: 35px;
  }
  #chartdiv {
    width: 100%;
    height: 500px;
    tspan {
      fill: #ffffff;
      letter-spacing: 0.05em;
      font-size: 16px;
    }
  }

  .people {
    margin: 10px 0;
    font-size: 26px;
    color: #ffffff;
    letter-spacing: 0.05em;
  }

  .btn_clear {
    position: absolute;
    bottom: 5px;
    right: 5px;
    padding: 15px;
    background: #2196f3;
    color: #ffffff;
    letter-spacing: 0.05em;
    font-size: 18px;
  }
  .wrap_confirm {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.781);
    .popup_confirm {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 95%;

      .btn {
        color: #ffffff;
        letter-spacing: 0.05em;
        border: 2px solid #ffffff;
        padding: 10px 25px;
        margin: 10px;
        font-size: 33px;
      }
    }
  }
}
@media screen and (max-width: 568px) {
  .wrap_chart {
    #chartdiv {
      width: 100%;
      height: 500px;
      tspan {
        font-size: 14px;
      }
    }
    .people {
      font-size: 16px;
    }
  }
}
</style>
