    </div>
    <div class="wrap_lable" v-if="mainContent">
      <div class="lable">Укажите места, где Вы встречали рекламу нашей клиники.</div>
    </div>
    <buttons-desc
      @toggleView="toggleView"
      v-if="mainContent"
      @set-no-active="setNoActive"
      :password="password"
      :buttons="buttons"
      :noActivityButtons="noActivityButtons"
