    <graphics v-if="!mainContent" @closeGraph="closeGraph"></graphics>
    <div class="wrap_btn_send" v-if="mainContent">
      <div class="btn_send" @click="sendData">отправить</div>
    </div>

    <div class="thx_wind" v-if="showWindThx">
      <div class="text_thx">Спасибо!</div>
    </div>
  </div>
</template>

