  },
  components: {
    ButtonsDesc,
    Graphics
  },
  methods: {
    sendData() {
      this.$children[0].pickUpSelectedItems();
      this.showWindThx = !this.showWindThx;
      this.password.length = 0;
      setTimeout(() => {
        this.showWindThx = !this.showWindThx;
      }, 20000);
      this.noActivityButtons = true;
    },
    toggleView() {
      this.mainContent = false;
      this.setNoActive;
    },
    closeGraph() {
      this.mainContent = true;
      this.password.length = 0;
      this.clearButtons();
    },
    clearArray() {
      this.password.length = 0;
      this.clearButtons();
    },
    setNoActive() {
      if (this.buttons.filter(e => e.active).length) {
        this.noActivityButtons = false;
      } else {
        this.noActivityButtons = true;
      }
    },
    clearButtons() {
      this.buttons.forEach(element => {
        element.active = false;
      });
    }
  }
};
</script>

<style lang="less">
@font-face {
  font-family: "Bebas";
  src: url("../font/BebasNeue Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #47631d;
  font-family: "Bebas";
}
.wrap_app {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  display: flex;
  flex-direction: column;
  justify-content: center;

  .wrap_logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
    .logo {
      width: 345px;
    }
    .subscription {
      color: #aec937;
      font-size: 21px;
      letter-spacing: 0.05em;
    }
  }
  .wrap_lable {
    display: flex;
    justify-content: center;

    .lable {
      width: 454px;
      padding: 0px 100px;
      color: #aec937;
      letter-spacing: 0.06em;
      font-size: 29px;
      font-weight: 700;
      line-height: 35px;
      text-align: center;
      box-shadow: 2px 11px 15px rgba(100, 100, 100, 0, 5);
    }
  }
  .wrap_btn_send {
    margin: 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    .btn_send {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      line-height: 24px;
      padding: 25px 51px;
      font-size: 33px;
      background: rgba(222, 43, 52, 0.89);
      color: #ffffff;
      font-weight: bold;
      border-radius: 8px;
      text-transform: uppercase;
      letter-spacing: 0.05em;
      overflow: hidden;
      &:before {
        content: "";
        width: 2em;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        background: linear-gradient(
          to right,
          rgba(255, 255, 255, 0.1),
          rgba(255, 255, 255, 0.6),
          rgba(255, 255, 255, 0.2)
        );
        filter: blur(10px);
        transform: translateX(-40em) skew(-45deg);
        transition: 0.5s;
        animation: move-light 10s infinite;
        animation-timing-function: ease;
      }

      @keyframes move-light {
        0% {
          transform: translateX(-40em) skew(-45deg);
        }
        15% {
          transform: translateX(15em) skew(-45deg);
        }
        100% {
          transform: translateX(16em) skew(-45deg);
        }
      }
    }
  }
  .thx_wind {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.781);
    .text_thx {
      color: #ffffff;
      text-transform: uppercase;
      font-weight: bold;
      letter-spacing: 0.1em;
      font-size: 36px;
    }
    animation: appearance 1s;
  }
  @keyframes appearance {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
}
@media screen and (max-width: 568px) {
  .wrap_app {
    .wrap_lable {
      .lable {
        padding: 0px 55px;
        font-size: 18px;
        line-height: 24px;
      }
    }
    .wrap_btn_send {
      .btn_send {
        padding: 14px 31px;
        font-size: 21px;
      }
    }
    .thx_wind {
      .text_thx {
        font-size: 29px !important;
      }
    }
    .wrap_logo {
      .logo {
        width: 245px;
      }
    }
  }
}
</style>
